import React, { Component } from 'react';

export default class extends Component {
  constructor(props){
    super(props);
  }


  componentDidMount() {

  }


  render() {
    return (
        <div className="cadre">
          <div className="img"></div>
          <div className="info">
            <button>Info</button>
            <button>Add</button>
          </div>
          <div className="title">{this.props.anime.title}</div>
        <style jsx>{`
            .cadre {
              width: 225px;
              height: 318px;
              position: relative;
              margin-bottom: 15px;
            }
            .img {
              width: 100%;
              height: 100%;
              background: url('${this.props.anime.image_url}')
            }
            .title {
              position: absolute;
              bottom: 0;
              text-align: center;
              width: 100%;
              background-color: #111111e6;
              max-height: 45px;
              overflow: hidden;
              // color: #fff;
            }
            .info {
              width: 100%;
              height: 100%;
              position: absolute;
              top: 0;
              left: 0;
              background-color: #111111e6;
              display: flex;
            }
            .info button {
              width: 80px;
              height: 30px;
              border-radius: 18px;
              margin: auto;
            }
        `}</style>
        </div>
    );
  }
}
